@import "~bootstrap/scss/bootstrap";
@import "~bootstrap/scss/_variables";
@import "mixins";
@import url("https://p.typekit.net/p.css?s=1&k=ehg4daz&ht=tk&f=35473.35474.35477.35478&a=4359107&app=typekit&e=css");

@each $breakpoint in map-keys($grid-breakpoints) {
    @include media-breakpoint-up($breakpoint) {
        $infix: breakpoint-infix($breakpoint, $grid-breakpoints);

        @each $prop,
        $unit in (width: vw, height: vh) {

            @each $size in [25,
            50,
            75,
            85,
            100] {
                .#{$unit}#{$infix}-#{$size} {
                    #{$prop}: #{$size}#{$unit} !important;
                }
            }
        }
    }
}

.mr-n-gutter {
    margin-right: -$grid-gutter-width;
}

.bg-img::before {
    content: '';
    display: block;
    position: absolute;
    z-index: -1;
    bottom: 0;
    top: 0;
    left: 22vh;
    height: 100%;
    width: 100%;
    background: #2167B1;
    transform: skew(30deg, 0deg);
}

.bg-img {
    position: relative;
}

.text-box{
    max-width: 800px;
}